.container {
  height: 100vh;
  background-color: var(--darkblue);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  --light-blue: #00b7c2;
}

.title {
  font-size: 2rem;
  margin: 0.8rem;
}

.input-div {
  width: 40%;
  min-width: min-content;
  margin: 0.4rem;
  padding: 0.4rem;
  display: flex;
  flex-direction: column;
}

.input-div label {
  margin: 0.4rem;
  font-weight: bold;
}

.input-div input {
  height: 2.4rem;
  border-radius: 0.4rem;
  font-size: medium;
  padding-inline-start: 0.8rem;
  outline: none;
}

.input-div input::-webkit-outer-spin-button,
.input-div input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.input-div input[type="number"] {
  -moz-appearance: textfield;
}

.input-div input:focus {
  border: none;
  outline: none;
}

.input-div textarea {
  border-radius: 0.4rem;
  padding-inline: 0.6rem;
  padding-top: 0.4rem;
  font-size: large;
}

.input-div textarea:focus {
  border: none;
  outline: none;
}

.submit-button {
  border: 1px solid var(--light-blue);
  width: 8%;
  min-width: min-content;
  color: var(--white);
  background-color: var(--light-blue);
  display: inline-block;
  padding: 0.4rem 0.8rem;
  align-self: center;
  margin: 0.6em 0.2rem;
  border-radius: 0.3rem;
  font-family: "Roboto", sans-serif;
  font-size: large;
  text-align: center;
  font-weight: 300;
  transition: all 0.2s;
  cursor: pointer;
}

.submitted-div {
  margin-top: 2.6rem;
  width: 24%;
  height: 36%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submitted-div img {
  padding-left: 1.6rem;
  height: 72%;
}
.submit-button:hover {
  background-color: var(--darkblue);
}
