  padding: 1rem;
  color: #111;
  background-color: white;
  box-shadow: 0 3px 4px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  transition: 250ms ease-in;
}

.random button:hover {
  cursor: pointer;
  transform: translate(-50%, -50%) scale(1.1);

  transition: 250ms ease-in;
}

.descriptionContainer {
  border-radius: 1rem;
  padding: 3rem;
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  text-align: center;
  height: 50rem;
  width: 100%;
  background-color: #fff;
  max-width: 80vw;
  background-repeat: no-repeat;
  height: fit-content;
  box-shadow: 0 4px 12px 0 rgba(31, 38, 135, 0.37);
  margin: 2rem;
  /* background-image: url("https://st.depositphotos.com/1200570/1428/v/950/depositphotos_14284641-stock-illustration-stylized-elefants-seamless-pattern.jpg"); */
}
.descriptionContainer button {
  position: absolute;
  padding: 1rem;
  border: none;
  color: grey;
  font-size: 2rem;
  background: transparent;
  cursor: pointer;
  right: 1rem;
  top: 0.2rem;
}
.descriptionContainer button:hover {
  transform: scale(1.1);
  color: red;
}
.descriptionContainer h1 {
  font-family: "Quicksand", sans-serif;
  position: absolute;

  top: 1rem;
}
.descriptionContainer p {
  margin-top: 1rem;
  font-family: "Open Sans", sans-serif;
  text-align: justify;
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  max-width: 60%;
  margin: 0 auto;
  box-shadow: 0 4px 12px 0 rgba(31, 38, 135, 0.37);
  border: 1px solid rgba(255, 255, 255, 0.18);
}
.imageTest {
  border-radius: 1rem;
  display: inline-block;
  width: 100%;
  background-size: 100% auto;

  background-repeat: no-repeat;

  margin-top: 2rem;
  margin-bottom: 1rem;
}

@media screen and (min-width: 40rem) {
  .descriptionContainer img {
    width: 55%;
  }
}

@media screen and (max-width: 39rem) {
  .descriptionContainer {
    width: 100%;
    padding: 3rem 1rem;
    margin: 2rem 0;
    max-width: 100%;
  }
  .contentSection {
    padding: 0;
  }
  .descriptionContainer p {
    max-width: 100%;
    padding: 1rem;
  }
}
